/* ======================== remove browser default styles ======================== */
* {
    border: 0;
    box-sizing: border-box;
    list-style: none;
    margin: 0;
    outline: 0;
    padding: 0;
    text-decoration: none;
}





/* ======================== variables ======================== */
:root {
    --color-primary: #00701a;
    --color-success: #00bf8e;
    --color-warning: #f7c94b;
    --color-danger: #ef6c00;
    --color-danger-variant: rgba(247, 88, 66, 0.4);
    --color-white: #fff;
    --color-light: rgba(255,255,255,0.7);
    --color-black: #000;
    --color-bg: #00251a;
    --color-bg1: #004d40;
    --color-bg2: #39796b;
    --color-bgSoftware: #c94f7c;
    --color-bgData: #5870cb;

    --container-width-lg: 80%;
    --container-width-md: 90%;
    --container-width-sm: 94%;

    --transition: all 400ms ease;
}





/* ======================== general custom styles ======================== */
body {
    background: var(--color-bg);
    background-image: url('../images/bg-texture.png');
    color: var(--color-white);
    font-family: Roboto, serif;
    line-height: 1.7;
}

.container {
    margin: 0 auto;
    width: var(--container-width-lg);
}

section {
    padding: 6rem 0;
}

section h2 {
    text-align: center;
    margin-bottom: 4rem;
}

h1, h2, h3, h4, h5 {
    line-height: 1.2;
}

h1 {
    font-size: 2.4rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.6rem;
}

h4 {
    font-size: 1.3rem;
}

h5 {
    font-size: 1.1rem;
}

a {
    color: var(--color-white);
}

img {
    width: 100%;
    display: block;
    object-fit: cover;
}

.btn {
    display: inline-block;
    background: var(--color-white);
    color: var(--color-black);
    padding: 1rem 2rem;
    border: 1px solid transparent;
    font-weight: 900;
    letter-spacing: 3px;
    transition: var(--transition);
    border-radius: 0.5rem;
    cursor: pointer;
}

.btn-small {
    padding: 0.5rem;
    margin: 0.5rem;
}


.btn:hover {
    background: transparent;
    color: var(--color-white);
    border-color: var(--color-white);
}

.btn-primary {
    background: var(--color-danger);
    color: var(--color-white);
}

.badge__social {
    display: inline-block;
    padding: 0;
    font-size: 12px;
    font-weight: lighter !important;
    line-height: 30px;
    color: var(--color-white) !important;
    text-align: center;
    white-space: nowrap;
    vertical-align: baseline;
    background: var(--color-bg2);
    border-radius:30px;
    position: relative;
    top: -3px;
    height: 30px;
    width: 30px;
    transition: var(--transition);
}





/* ======================== ripple area effect ======================== */
.ripple {
    position: relative;
    display: inline-block;
    padding: 1rem 0.8rem;
    margin: 0;
    color: #fff;
    text-decoration: none;
    letter-spacing: 1.5px;
    line-height: 1.7rem;
    font-size: 1rem;
    border-radius: 2rem;
    background:  linear-gradient(90deg, rgba(0,51,0,0.5) 0%, rgba(76,140,74,0.5) 100%);
    overflow: hidden;
    transition: var(--transition);
    cursor: pointer;
}

.ripple span {
    position: absolute;
    background: #fff;
    transform: translate(-100px -150px);
    pointer-events: none;
    border-radius: 50%;
    animation: animate 2s linear infinite ;
}

.ripple:hover {
    transform: scale(1.08);
    color: lime;
}


@keyframes animate {
    0% {
        width: 0;
        height: 0;
        opacity: 0.5;
    }
    100% {
        width: 200px;
        height: 200px;
        opacity: 1;
    }
}





/* ======================== scrollbar effect ======================== */
::-webkit-scrollbar {
    width: 0;
}

#scrollPath {
    position: fixed;
    top: 0;
    right: 0;
    width: 10px;
    height: 100%;
    background: rgba(255, 255, 255, 0.05);
}

#progressbar {
    position: fixed;
    top: 0;
    right: 0;
    width: 10px;
    background: linear-gradient(to top, #008aff, #00ffe7);
    animation: animate 5s linear infinite;
    z-index: 15;
}

@keyframes animate {
    0%, 100% {
        filter: hue-rotate(0deg);
    }

    50% {
        filter: hue-rotate(360deg);
    }
}

#progressbar::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to top, #008aff, #00ffe7);
    filter: blur(10px);
}

#progressbar::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to top, #008aff, #00ffe7);
    filter: blur(30px);
}






/* ======================== navbar ======================== */
nav {
    background: transparent;
    width: 100vw;
    height: 5rem;
    position: fixed;
    top: 0;
    z-index: 11;
}

/*  navbar change on scroll with main.js  */
.window-scroll {
    background: var(--color-primary);
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.3);
    transition: all 0.5s ease;
}
.nav__container {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

nav button {
    display: none;
}

.nav__menu {
    display: flex;
    align-items: center;
    gap: 4rem;
}

.nav__menu a{
    font-size: 0.9rem;
    transition: var(--transition);
}

.nav__menu a:hover {
    color: var(--color-bg2);
}





/* ======================== header ======================== */
.index__header {
    position: relative;
    top: 5rem;  /* as height of navbar*/
    overflow: hidden;
    height: 100vh;
    margin-bottom: 5rem;
}

.header__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 5rem;
    height: 100%;
}

.header__right h1 {
    text-transform: uppercase;
    font-weight: 800;
}

.header__right p {
    margin: 1rem 0 2.4rem;
}





/* ======================== categories ======================== */
.categories {
    background: var(--color-bg1);
    height: 43rem;
}

.categories h1 {
    line-height: 1;
    margin-bottom: 3rem;
}

.categories__container {
    display: grid;
    grid-template-columns: 55% 45%;
    gap: 2rem;
}

.categories__left {
    margin-left: 3rem;
}

.categories__left p {
    font-size: 1.2rem;
    margin: 1rem 0 3rem;
}

.categories__right {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.2rem;
}

.category {
    padding: 2rem 1rem;
    border-radius: 2rem;
    transition: var(--transition);
    width: 180px;
    border: transparent;
}

.category:hover {
    background: transparent;
    z-index: 1;
    cursor: pointer;
}

.categoryCs{
    background: var(--color-bgSoftware);
}
.categoryDs{
    background: var(--color-bgData);
}

.category:nth-child(2) .category__icon {
    background: var(--color-danger);
}
.category:nth-child(3) .category__icon {
    background: var(--color-success);
}
.category:nth-child(4) .category__icon {
    background: var(--color-warning);
}
.category:nth-child(5) .category__icon {
    background: var(--color-success);
}

.category__icon {
    background: var(--color-primary);
    padding: 0.2rem 0.5rem;
    font-size: 2rem;
    border-radius: 0.9rem;
}

.category h5 {
    margin: 2rem 0 1rem;
}

.category p {
    font-size: 0.8rem;
}





/* ======================== projects ======================== */
.badge--htmlcss {
    background: var(--color-bg);
    position: absolute;
    top: 6%;
    right: -5%;
}

.badge--bootstrap {
    background: var(--color-black);
    position: absolute;
    top: 6%;
    right: 10%;
}
.badge--js {
    background: var(--color-bg2);
    position: absolute;
    top: 6%;
    right: 5%;
}

.badge {
    border-radius: 0.5rem;
    width: 100px;
    font-size: 0.7rem;
    padding: 0.5rem 0.5rem;
    transform: rotate(90deg);
    white-space: nowrap;
    letter-spacing: 1px;
    font-weight: 800;
    color: var(--color-warning);
    transform: translateY(-100px);
    transition: var(--transition);
}


.projects__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
}

.project {
    background: var(--color-bg1);
    border-radius: 0.5rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
    overflow: hidden;
    position: relative;
}

.project:hover {
    background: transparent;
    border-color: var(--color-primary);
}

.project:hover .badge {
    transform: translateY(0);
    transform: rotate(90deg);
}

.project__info {
    padding: 2rem;
}

.project__info p {
    margin: 1.2rem 0 2rem;
    font-size: 0.85rem;
}





/* ======================== filter ======================== */
.filter__buttons {
    display: flex;
    justify-content: center;
}

.filter-box {
    display: none;
}

.show {
    display: block;
}





/* ======================== abouts ======================== */
.abouts {
    background: var(--color-bg1);
    box-shadow: inset 0 0 3rem rgba(0, 0, 0, 0.5);
}

.abouts__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
}

.about {
    padding: 2rem;
    display: flex;
    align-items: center;
    gap: 1.4rem;
    height: fit-content;
    background: var(--color-primary);
    cursor: pointer;
    border-radius: 0.5rem;
}

.about h4 {
    font-size: 1rem;
    line-height: 2.2;
}

.about__icon {
    align-self: flex-start;
    font-size: 1.7rem;
}

.about p {
    margin-top: 0.8rem;
    display: none;
}

/* answer shown with javascript */
.about.open p {
    display: block;
}





/* ======================== quotes ======================== */
.quotes__container {
    overflow-x: hidden;
    position: relative;
    margin-bottom: 5rem;
    margin-top: 5rem;
}
.quote {
    padding-top: 2rem;
    margin-bottom: 5rem;
}

.avatar {
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto 1rem;
    border: 1rem solid var(--color-bg1);
}

.quote__info {
    text-align: center;
}

.quote__body {
    background: var(--color-primary);
    padding: 2rem;
    margin-top: 3rem;
    position: relative;
}

.quote__body::before {
    content: "";
    display: block;
    background: linear-gradient(
        135deg, 
        transparent, 
        var(--color-primary), 
        var(--color-primary));
    width: 3rem;
    height: 3rem;
    position: absolute;
    left: 50%;
    top: -1.5rem; /* half the height */
    transform: rotate(45deg);
}





/* ======================== footer ======================== */
footer {
    background: var(--color-bg1);
    padding-top: 5rem;
    font-size: 0.85rem;
}

.footer__container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 5rem;
}

.footer__container > div h4 {
    margin-bottom: 1.2rem;
}

.footer__1 p {
    margin: 0 0 2rem;
}

footer ul li {
    margin-bottom: 0.7rem;
}

footer ul li a:hover {
    text-decoration: underline;
}

.footer__socials {
    display: flex;
    gap: 1rem;
    font-size: 1.3rem;
    margin-top: 2rem;
}

.footer__copyright {
    text-align: center;
    margin-top: 4rem;
    padding: 1.2rem 0;
    border-top: 1px solid var(--color-bg2);
}





/* ======================== SCREEN : tablets ======================== */
@media screen and (max-width: 1024px) {
    /* ================ general styles ================ */
    .container {
        width: var(--container-width-md);
    }
    .h1 {
        font-size: 2.2rem;
    }
    .h2 {
        font-size: 1.7rem;
    }
    .h3 {
        font-size: 1.4rem;
    }
    .h4 {
        font-size: 1.2rem;
    }
    

    /* ================  navbar ================ */
    nav button {
        display: inline-block;
        background: transparent;
        font-size: 1.8rem;
        color: var(--color-white);
        cursor: pointer;
    }
    nav button#close-menu-btn {
        display: none;
    }
    .nav__menu {
        position: fixed;
        top: 5rem;
        left: 5%;
        height: fit-content;
        width: 18rem;
        flex-direction: column;
        gap: 0;
        display: none;
    }
    .nav__menu li {
        width: 100%;
        height: 5.8rem;
        animation: animateNavItems 400ms linear forwards;
        transform-origin: top left;
        opacity: 0;
    }
        /* delay every item to come after last one */
    .nav__menu li:nth-child(2) {
        animation-delay: 200ms;
    }
    .nav__menu li:nth-child(3) {
        animation-delay: 400ms;
    }
    .nav__menu li:nth-child(4) {
        animation-delay: 600ms;
    }
    @keyframes animateNavItems {
        0% {
            transform: rotateZ(90deg);
        }
        100% {
            transform: rotateZ(0);
            opacity: 1;
        }
    }
    .nav__menu li a {
        background: var(--color-primary);
        box-shadow: -4rem 6rem 10rem rgba(0, 0, 0, 0.6);
        width: 100%;
        height: 100%;
        display: grid;
        place-items: center;
    }
    .nav__menu li a:hover {
        background: var(--color-bg2);
        color: var(--color-white);
    }


    /* ================  header ================ */
    .index__header {
        height: 100vh;
        margin-bottom: 5rem;
    }
    .header__container {
        gap: 0.5rem;
        padding-bottom: 3rem;
    }


    /* ================  categories ================ */
    .categories {
        height: fit-content;
    }
    .categories__container {
        grid-template-columns: 1fr;
        gap: 3rem;
    }
    .categories__right {
        margin-right: 0;
        justify-items: center;
    }


    /* ================  projects ================ */
    .projects {
        margin-top: 0;
    }
    .projects__container {
        grid-template-columns: 1fr 1fr;
    }


    /* ================  abouts ================ */
    .abouts__container {
        grid-template-columns: 1fr;
    }
    .about {
        padding: 1.5rem;
    }


    /* ================  footer ================ */
    .footer__container {
        grid-template-columns: 1fr 1fr;
    }
}





/* ======================== SCREEN : mobile ======================== */
@media screen and (max-width: 600px) {
    /* ================ general styles ================ */
    .container {
        width: var(--container-width-sm);
    }


    /* ================ navbar ================ */
    .nav__menu {
        left: 3%;
    }


    /* ================ header ================ */
    .index__header {
        height: 100vh;
    }
    .header__container {
        grid-template-columns: 1fr;
        text-align: center;
    }
    .header__right p {
        margin-bottom: 1.3rem;
    }


    /* ================ categories ================ */
    .categories__right {
        grid-template-columns: 1fr 1fr;
        gap: 0.7rem;
    }
    .category {
        padding: 1rem;
        border-radius: 1rem;
    }
    .category__icon {
        margin-top: 4px;
        display: inline-block;
    }


    /* ================ projects ================ */
    .projects__container {
        grid-template-columns: 1fr;
    }


    /* ================ quotes ================ */
    .quote__body {
        padding: 1.2rem;
    }


    /* ================ footer ================ */
    .footer__container {
        grid-template-columns: 1fr;
        text-align: center;
        gap: 2rem;
    }
    .footer__1 p {
        margin: 1rem auto;
    }
    .footer__socials {
        justify-content: center;
    }
}










